import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { API_BASE_URL } from '../config';
const NewRegister = () => {
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');
  const [teamId, setTeamId] = useState('');
  const [username, setUsername] = useState('');
  const [language, setLanguage] = useState(1);  // 1
  const [studyLanguage, setStudyLanguage] = useState(1);  // 1
  const [success, setSuccess] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  // 
  const languageOptions = [
    { id: 1, label: "" },
    { id: 2, label: "English" },
    { id: 3, label: "Portugus" },
    { id: 4, label: "Espaol" },
    { id: 5, label: " (Simplified Chinese)" },
    { id: 6, label: " (Traditional Chinese)" },
    { id: 7, label: "" },
    { id: 8, label: "Tagalog" },
    { id: 9, label: "Ting Vit" },
    { id: 10, label: "Bahasa Indonesia" },
    { id: 11, label: "" },
  ];
  const handleRegister = (e) => {
    e.preventDefault();
  
    // 
    console.log("User ID:", userId);
    console.log("Team ID:", teamId);
    console.log("Password:", password);
    console.log("Username:", username);
    console.log("Main Language:", language);
    console.log("Learn Language:", studyLanguage);
  
    if (userId === '' || password === '' || teamId === '' || username === '') {
      setError('Please fill in all fields!: ');
      return;
    }
  
    // POST
    axios.post(`${API_BASE_URL}/register`, {
      user_id: userId,
      team_id: teamId,
      password: password,
      name: username,  // FastAPI 'name' 
      main_language: language,
      learn_language: studyLanguage,
    })
    .then(response => {
      setSuccess('OK! ');
      setError('');
      navigate('/'); // 
    })
    .catch(error => {
      console.error('Registration error:', error);  // 
      if (error.response) {
        if (error.response.data.detail === "User ID already exists") {
          setError("ERROR: ID");
        } else {
          setError(`The User ID already exists: ${error.response.data.detail || error.message}`);
        }
      } else if (error.request) {
        setError('ERROR: ');
      } else {
        setError(`ERROR: ${error.message}`);
      }
    });
  };
  
  

  const handleGoBack = () => {
    navigate("/startpage"); // 
  };
  return (
    <div className="container add-course" style={{ maxWidth: "400px", margin: "0 auto", padding: "20px" }}>
      <h1 style={{ textAlign: "center" }}>Create a New Account</h1>
      <form onSubmit={handleRegister} style={{ backgroundColor: "#F9F9F9", padding: "20px", borderRadius: "8px" }}>
        <div style={{ marginBottom: "15px" }}>
          <label>User ID:</label>
          <input
            type="text"
            value={userId}
            onChange={(e) => setUserId(e.target.value)}
            style={{
              width: "100%",
              padding: "10px",
              marginTop: "5px",
              borderRadius: "5px",
              border: "1px solid #ccc",
            }}
          />
        </div>
        <div style={{ marginBottom: "15px" }}>
          <label>Team ID:</label>
          <input
            type="text"
            value={teamId}
            onChange={(e) => setTeamId(e.target.value)}
            style={{
              width: "100%",
              padding: "10px",
              marginTop: "5px",
              borderRadius: "5px",
              border: "1px solid #ccc",
            }}
          />
        </div>
        <div style={{ marginBottom: "15px" }}>
          <label>Password:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            style={{
              width: "100%",
              padding: "10px",
              marginTop: "5px",
              borderRadius: "5px",
              border: "1px solid #ccc",
            }}
          />
        </div>
        <div style={{ marginBottom: "15px" }}>
          <label>User Name:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            style={{
              width: "100%",
              padding: "10px",
              marginTop: "5px",
              borderRadius: "5px",
              border: "1px solid #ccc",
            }}
          />
        </div>
        <div style={{ marginBottom: "15px" }}>
          <label>Main Language:</label>
          <select
            value={language}
            onChange={(e) => setLanguage(Number(e.target.value))}  // 
            style={{
              width: "100%",
              padding: "10px",
              marginTop: "5px",
              borderRadius: "5px",
              border: "1px solid #ccc",
            }}
          >
            {languageOptions.map((lang) => (
              <option key={lang.id} value={lang.id}>
                {lang.label}
              </option>
            ))}
          </select>
        </div>
        <div style={{ marginBottom: "15px" }}>
          <label>Learn Language:</label>
          <select
            value={studyLanguage}
            onChange={(e) => setStudyLanguage(Number(e.target.value))}  // 
            style={{
              width: "100%",
              padding: "10px",
              marginTop: "5px",
              borderRadius: "5px",
              border: "1px solid #ccc",
            }}
          >
            {languageOptions.map((lang) => (
              <option key={lang.id} value={lang.id}>
                {lang.label}
              </option>
            ))}
          </select>
        </div>
                <button
          type="submit"
          style={{
            width: "100%",
            padding: "10px",
            backgroundColor: "#FF9800", // 
            color: "white",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
          }}
        >
          New
        </button>

        <div style={{ marginTop: "20px", textAlign: "left" }}>
          <button
            onClick={handleGoBack}
            style={{
              backgroundColor: "#4CAF50", // 
              color: "#fff",
              padding: "8px 12px",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
            }}
          >
             Back
          </button>
        </div>

      </form>
      {success && <p style={{ color: "green", marginTop: "10px" }}>{success}</p>}
      {error && <p style={{ color: "red", marginTop: "10px" }}>{error}</p>}
    </div>
    
  );
};
export default NewRegister;