import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { API_BASE_URL } from '../config';
const Question3 = () => {
  const { diaryId } = useParams(); // URLdiaryId
  const [quiz, setQuiz] = useState(); // 
  const [selectedOption, setSelectedOption] = useState(null);
  const [selectAnswer, setSelectAnswer] = useState(null);
  const navigate = useNavigate();

  // 
  const alreadyQuiz = async () => {
    try {
      const token = localStorage.getItem("access_token"); // 
      const response = await axios.get(`${API_BASE_URL}/already_quiz/${diaryId}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      console.log("Already quiz response:", response.data);
      return response.data.already; // true false 
    } catch (err) {
      console.error("ERROR:", err);
      return true; // 
    }
  };

  // 
  const fetchQuiz = async () => {
    try {
      const already = await alreadyQuiz(); // 
      if (already) {
        alert("This quiz is already answered : ");
        navigate("/Chat"); // 
        return;
      }

      const token = localStorage.getItem("access_token"); // 
      const response = await axios.get(`${API_BASE_URL}/get_same_quiz/${diaryId}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      console.log(response); // 

      const quizzes = response.data.quizzes;

      // quiz_id 2
      if (response.data.quizzes && response.data.quizzes.length > 1) {
        const sortedQuizzes = response.data.quizzes.sort((a, b) => a.quiz_id - b.quiz_id);
        setQuiz(sortedQuizzes[2]); // 3 quiz_id 
      } else {
        console.error("2");
      }
    } catch (err) {
      console.error("ERROR:", err);
    }
  };

  useEffect(() => {
    fetchQuiz();
  }, [diaryId]);

  const handleOptionChange = (key) => {
    setSelectedOption(key); // 
    setSelectAnswer(key);
  };

  const submitAnswer = async () => {
    if (selectAnswer == null) {
      alert("Please select an answer. : ");
      return false; // false
    }
    const token = localStorage.getItem("access_token");
    const answerData = {
      quiz_id: quiz.quiz_id,
      diary_id: quiz.diary_id,
      choices: selectAnswer,
    };
    console.log(":", answerData); // 
    try {
      await axios.post(`${API_BASE_URL}/create_answer`, answerData, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      console.log("");
      return true; // true
    } catch (err) {
      console.error(":", err);
      return false; // false
    }
  };

  const handleSubmit = async () => {
    const success = await submitAnswer();
    if (success) {
      navigate(`/Answer3/${quiz.diary_id}`, { state: { selectedOption } }); // 
    } else {
      alert("Please select an answer. : ");
    }
  };

  if (!quiz) {
    return <div>Loading...</div>; // 
  }

  return (
    <div style={styles.container}>
      <h3>Q3 <u>{quiz.question}</u></h3>
      <div style={styles.options}>
        {Object.entries(quiz.choices).map(([key, option], index) => (
          <div key={index} style={styles.option}>
            <input
              type="radio"
              id={`option-${index}`}
              name="quiz"
              value={option}
              onChange={() => handleOptionChange(key)} // 
            />
            <label htmlFor={`option-${index}`} style={styles.label}>
              {key.toUpperCase()}. {option}
            </label>
          </div>
        ))}
      </div>
      <button onClick={handleSubmit} style={styles.submitButton}>
        Answer
      </button>
    </div>
  );
};

const styles = {
  container: {
    fontFamily: "Arial, sans-serif",
    padding: "100px",
    maxWidth: "1000px",
    border: "1px solid #ccc",
    borderRadius: "10px",
    backgroundColor: "#F9F9F9",
    margin: "0 auto",
  },
  options: {
    marginTop: "20px",
  },
  option: {
    display: "flex",
    alignItems: "center",
    marginBottom: "15px",
  },
  label: {
    marginLeft: "10px",
    flexGrow: 1,
    color: "#333", // 
  },
  submitButton: {
    marginTop: "30px",
    backgroundColor: "#FFA500", // 
    color: "#fff",
    border: "none",
    padding: "15px 30px",
    borderRadius: "10px",
    cursor: "pointer",
    fontSize: "16px",
    transition: "background-color 0.3s", // 
  },
};

export default Question3;
